import React, { useEffect, useState } from "react";
import Draggable from "react-draggable";
import LetterCss from './Letter.css'

export default function Letter ({letter, dragged, setCurrentDrag, id, stopped, classes, clicked}) { 

    const [beingDragged, setBeingDragged] = useState(false)

    const renderLetter = (l) => {
        if(l == '?') return 'ie'
        if(l == '/') return 'gÄ§'

        return l
    }

    const handleStart = (e) => {
        console.log(e.target.style.zIndex)
        e.target.style.zIndex = 1000
        setBeingDragged(true)
        
        // Handle the drag over event here
    }


    const stoppa = (e) => {
        stopped(e)
        setBeingDragged(false)

    }
    
    

    return (
        <>  
            {!dragged && letter != '.' ?
                <Draggable onStart={handleStart} onStop={stoppa}>
                    <span  id={id} className={'grid_cell '+ (beingDragged ? 'big_mode':'') + (classes[id[0]][id[1]])}>
                        {renderLetter(letter)}
                    </span>
                </Draggable>
            :
                <span  id={id} className={'grid_cell ' + (letter == '.' ? 'dot':'')  + (letter !='.' ? + (classes[id[0]][id[1]]) :'')}>
                    {letter != '.' ? renderLetter(letter) : ''}
                </span>
            }
        </>
    )
}