import React, { useEffect, useState } from "react";
import GridCss from './GridCss.css'
import Draggable from "react-draggable";
import Letter from '../Letter/Letter'
export default function Grid ({answer, initial_state}) { 
    const guessLimit = 15
    const current_state = answer;
    const [grid, setGrid] = useState(initial_state)
    const [classes, setClasses] = useState([
        ['.', '.', '.', '.', '.'],
        ['.', '.', '.', '.', '.'],
        ['.', '.', '.', '.', '.'],
        ['.', '.', '.', '.', '.'],
        ['.', '.', '.', '.', '.']
    ])

    const reducer = {
        '0,1':'0,3',
        '2,3':'2,1',
        '4,3':'4,1',
        '3,0':'1,0',
        '3,2':'1,2',
        '3,4':'1,4'
    }

    const mapping = {
        // easy in horizontal
        '0,1':[[0,0], [0,1], [0,2], [0,3], [0,4]],
        '0,3':[[0,0], [0,1], [0,2], [0,3], [0,4]],

        '2,1':[[2,0], [2,1], [2,2], [2,3], [2,4]],
        '2,3':[[2,0], [2,1], [2,2], [2,3], [2,4]],

        '4,1':[[4,0], [4,1], [4,2], [4,3], [4,4]],
        '4,3':[[4,0], [4,1], [4,2], [4,3], [4,4]],

        // easy in vertical
        '1,0':[[0,0], [1,0], [2,0], [3,0], [4,0]],
        '3,0':[[0,0], [1,0], [2,0], [3,0], [4,0]],

        '1,2':[[0,2], [1,2], [2,2], [3,2], [4,2]],
        '3,2':[[0,2], [1,2], [2,2], [3,2], [4,2]],

        '1,4':[[0,4], [1,4], [2,4], [3,4], [4,4]],
        '3,4':[[0,4], [1,4], [2,4], [3,4], [4,4]],

        // top left corner
        '0,0':[[0,0], [0,1], [0,2], [0,3], [0,4], [1,0], [2,0], [3,0], [4,0] ],

        // bottom left corner
        '4,0':[[4,0], [4,1], [4,2], [4,3], [4,4], [0,0], [1,0], [2,0], [3,0] ],

        // top right corner
        '0,4':[[0,0], [0,1], [0,2], [0,3], [0,4], [1,4], [2,4], [3,4], [4,4]],

        // bottom right corner
        '4,4': [[4,0], [4,1], [4,2], [4,3], [4,4], [0,4], [1,4], [2,4], [3,4]],

        // top T
        '0,2': [[0,0], [0,1], [0,2], [0,3], [0,4], [1,2], [2,2], [3,2], [4,2]],

        // left T
        '2,0': [[0,0], [1,0], [2,0], [3,0], [4,0], [2,1], [2,2], [2,3], [2,4] ],

        // bottom T
        '4,2': [[4,0], [4,1], [4,2], [4,3], [4,4], [0,2], [1,2], [2,2], [3,2]],

        // right T
        '2,4': [[2,0], [2,1], [2,2], [2,3], [2,4], [0,4], [1,4], [3,4], [4,4]],

        // middle +
        '2,2': [[2,0], [2,1], [2,2], [2,3], [2,4], [0,2], [1,2], [3,2], [4,2]]
    }

    const [currentDrag, setCurrentDrag] = useState(false)


    useEffect( () =>{
        updateClasses()
    }, [])

    useEffect(()=>{
        updateClasses()
    }, [grid])

    useEffect( () =>{
        if(currentDrag)
            setCurrentDrag(false)
    }, [currentDrag])

    const entered = (id) => {
        id.preventDefault()
    }

    const calculateIoU = (rect1, rect2) => {
        // Calculate the coordinates of the intersection rectangle
        const intersectionLeft = Math.max(rect1.left, rect2.left);
        const intersectionTop = Math.max(rect1.top, rect2.top);
        const intersectionRight = Math.min(rect1.right, rect2.right);
        const intersectionBottom = Math.min(rect1.bottom, rect2.bottom);
      
        // Calculate the dimensions of the intersection rectangle
        const intersectionWidth = Math.max(0, intersectionRight - intersectionLeft);
        const intersectionHeight = Math.max(0, intersectionBottom - intersectionTop);
      
        // Calculate the area of intersection
        const intersectionArea = intersectionWidth * intersectionHeight;
      
        // Calculate the area of the union
        const rect1Area = (rect1.right - rect1.left) * (rect1.bottom - rect1.top);
        const rect2Area = (rect2.right - rect2.left) * (rect2.bottom - rect2.top);
        const unionArea = rect1Area + rect2Area - intersectionArea;
      
        // Calculate the IoU
        const iou = intersectionArea / unionArea;
      
        return iou;
    }      

    const stopped = (e) => {
        
        let source_element = e.srcElement;
        source_element.style.zIndex = 10


        let elements = document.getElementsByClassName('grid_cell')
        const rect1 = source_element.getBoundingClientRect();

        let max_iou = 0;
        let max_iou_ind = -1;
        
        for(let i = 0; i < elements.length; i ++){
            let element = elements[i]

            if (element.id == source_element.id){
                continue
            }

            let rect2 = element.getBoundingClientRect();
            let iou = calculateIoU(rect1, rect2)
            if(iou == 0) {
                continue
            }else{
                if(iou > max_iou){
                    max_iou = iou
                    max_iou_ind = element.id
                }
            }
        }

        setCurrentDrag(true)

        let sx = parseInt( source_element.id.split(',')[0])
        let sy = parseInt( source_element.id.split(',')[1])

        if(max_iou_ind == -1){
            return
        }

        let tx = parseInt( max_iou_ind.split(',')[0])
        let ty = parseInt( max_iou_ind.split(',')[1])

        if(grid[tx][ty] == '.'){
            return
        }

        setGrid((grid) => {

            const newGrid = grid.map((row) => [...row]); 

            let a = newGrid[sx][sy]
            newGrid[sx][sy] = newGrid[tx][ty]
            newGrid[tx][ty] = a

            return newGrid
        })

       
    }

    const updateClasses = () =>{

        //console.log('answer', answer)

        const newClasses = classes.map((row) => [...row]); 

        // reset class colours
        for(let i = 0; i < grid.length; i ++){
            for(let j = 0; j < grid[0].length; j ++){
                newClasses[i][j] = 'gray'
            }
        }

        let greens = {}
        let yellows = {}

        // set greens
        for(let i = 0; i < grid.length; i ++){
            for(let j = 0; j < grid[0].length; j ++){

                if(answer[i][j] == grid[i][j]){
                    newClasses[i][j] = 'green'
                    greens[i+','+j] = 1
                }
            }
        }

        for(let i = 0; i < grid.length; i ++){
            for(let j = 0; j < grid[0].length; j ++){
                let current_id = i+','+j
                let current_letter = grid[i][j]

                if(current_id in greens){
                    continue
                }
                if(current_id in mapping){
                    let letters = mapping[current_id]
                    //console.log('letters', letters)
                    let letter_map = {

                    }

                    for(let f = 0; f < letters.length; f ++){
                        let id = letters[f][0]+','+letters[f][1]
                        letter_map[id] = 1
                    }

                    let original_counts = {}
                    //console.log('yellows', yellows)
                    //console.log('letter_map', letter_map)

                    for(let k = 0; k < letters.length; k ++){
                        let x = letters[k][0]
                        let y = letters[k][1]

                        let original_letter = answer[x][y]
                        let id = x+','+y



                        //console.log(current_id, 'adding ', original_letter)

                        if(original_letter in original_counts){
                            original_counts[original_letter] += 1
                        }else{
                            original_counts[original_letter] = 1
                        }
                    }

                    let current_greens = Object.keys(greens)

                    for(let cg = 0; cg < current_greens.length; cg ++){
                        let id = current_greens[cg]

                        let x = parseInt(id[0])
                        let y = parseInt(id[2])

                        let cl = grid[x][y]

                        //console.log(current_id, 'considering green removing', cl, 'at', x,y)
                        //console.log(original_counts, letter_map)
                        if(cl in original_counts && id in letter_map){
                        //console.log(current_id, ' green removing', cl, 'at', x,y)
                        original_counts[cl] -= 1
                        }

                    }

                    let current_yellows = Object.keys(yellows)
                    for(let cy = 0; cy < current_yellows.length; cy ++){
                        let id = current_yellows[cy]

                        let x = parseInt(id[0])
                        let y = parseInt(id[2])

                        let cl = grid[x][y]

                        //console.log(current_id, 'considering removing', cl, 'at', x,y)
                        if(cl in original_counts && id in letter_map){
                            original_counts[cl] -= 1
                        }

                    }

                    //console.log(current_id, 'original_counts', original_counts)

                    if(original_counts[current_letter] > 0){
                        newClasses[i][j] = 'yellow'
                        //console.log(current_id, 'set to yellow  ')
                        yellows[current_id] = 1
                    }
                }
            }
        }

        setClasses(newClasses)
    }

    return (
        <>  
        <div className="all_holder">
            <div className='grid_holder'>
                {
                    grid.map ((row, i) => {
                        return <span className='grid_row'>
                            {
                                row.map((letter, j) => 
                                    <Letter letter={letter} dragged={currentDrag} id={[i,j]} setCurrentDrag={setCurrentDrag} stopped={stopped} classes={classes}/>
                                )
                            }
                        </span>
                    })
                }
            </div>
        </div>
            
        </>
    )
}