import logo from './logo.svg';
import './App.css';
import Grid from './Grid/Grid'
import random from "random-seed"
import withReactContent from 'sweetalert2-react-content'
import React, { Component }  from 'react';
import '@sweetalert2/theme-dark/dark.css'

import Swal from 'sweetalert2/dist/sweetalert2.js'

import answers from './answers.json'
import { useEffect, useState } from 'react';


function App() {
  
  const [gameMode, setGameMode] = useState(-1)
  const MySwal = withReactContent(Swal)


  let dayOfYear = function(){
    var now = new Date();
    var start = new Date(now.getFullYear(), 0, 0);
    var diff = (now - start) + ((start.getTimezoneOffset() - now.getTimezoneOffset()) * 60 * 1000);
    var oneDay = 1000 * 60 * 60 * 24;
    return Math.floor(diff / oneDay);
  }

  let getTodayRandom = function(){
    
    return random.create(dayOfYear() + (new Date().getFullYear())).random()
  }

  useEffect( ()=>{
      setTimeout( ()=> {
        MySwal.fire({
          title: <p>Merhba ghal Wofil.</p>,
          position:'top',
          html: <div style={{    display: 'flex'}}>
            Instructions... taparsi
          </div>,
          confirmButtonText:'Ta illum',
          cancelButtonText:'Li tkun',
          background:'black',
          cancelButtonColor:"red",
          showConfirmButton: true,
          showCancelButton:true,
          allowOutsideClick:false,
          allowEscapeKey:false
        }).then((value) =>{
  
          if(value.isConfirmed){
            setGameMode(Math.floor(getTodayRandom() * 100))
            
          }else if(value.isDismissed){
            setGameMode(Math.floor(Math.random() * 100))
          }
  
        })
      }, 500)

      
  }, [])




  return (
    <div className="App">
      {gameMode == -1 ?
      '' :
      <Grid answer={answers[gameMode][0]} initial_state={answers[gameMode][1]}></Grid>
      }
      </div>
  );
}

export default App;
